body {
    font-family: 'Lato';
}

.fa-btn {
    margin-right: 6px;
}

ul.listasgrid{
		  
	margin-bottom:20px;
	overflow:hidden;
	border-top:1px solid #ccc;
}
ul.listasgrid li{
	line-height:1.5em;
	border-bottom:1px solid #ccc;
	float:left;
	display:inline;
	padding: 10px;
	min-height: 170px;
}
#double li  { width:50%;}
#triple li  { width:33.333%; }
#quad li    { width:25%; }
#six li     { width:16.666%; }